.content{width:60%;margin:0 auto;display:flex;align-items:center;justify-content:center;padding-top:2em;min-height:85vh}@media (max-width: 1024px){.content{width:70%}}@media (max-width: 550px){.content{width:90%;padding-top:1em}}.giftImg{position:fixed;object-fit:cover}.giftImg-big{right:0;max-width:17%}@media (max-width: 1024px){.giftImg-big{max-width:15%}}@media (max-width: 550px){.giftImg-big{max-width:33%}}.giftImg-cut{max-width:8%;left:4%;top:2%}@media (max-width: 1024px){.giftImg-cut{max-width:10%}}@media (max-width: 550px){.giftImg-cut{max-width:17%}}.giftImg-small{max-width:17%;right:1%;bottom:1%}@media (max-width: 1024px){.giftImg-small{max-width:30%}}@media (max-width: 550px){.giftImg-small{max-width:45%}}@media (max-width: 1024px){.hiddenImg{display:none}}

.welcome{display:flex;flex-direction:column;justify-content:center;position:relative}.welcome__btn-container{position:absolute;bottom:0;width:100%}.welcome__btn{font-size:calc(16px + 14 * ((100vw - 320px) / 1380));font-family:'Caveat', cursive;text-transform:uppercase;font-weight:400;letter-spacing:0.1em;padding:0.5em 1em;background:#000000;color:#ffffff;border:4px #ffffff solid;display:block;margin:0 auto}.welcome__btn:hover{box-shadow:1px 0 29px 10em rgba(0,0,0,0.23) inset;background:transparent;transition:all .4s ease-in-out}@media (max-width: 425px){.welcome__btn{margin-bottom:-12%}}.welcome__img{display:block;object-fit:cover;max-width:103%}@media (max-width: 425px){.welcome{height:50%}}

.test{font-size:calc(12px + 10 * ((100vw - 320px) / 1380));background-color:#ffffff;background-image:url("https://www.transparenttextures.com/patterns/blizzard.png");width:100%;min-height:24em;padding:3em 2.5em;color:#524f4e;font-family:'Montserrat', sans-serif;font-weight:400;line-height:1.3em;border:1px #ffeede solid;border-radius:2em}@media (max-width: 425px){.test{margin-top:30%}}.test__item{display:flex;justify-content:space-between}.test__item p{width:50%}@media (max-width: 768px){.test__item{flex-direction:column}.test__item p{width:100%}}

.testAnswers{display:flex;flex-direction:column;margin-left:4%;width:100%;max-width:40%}@media (max-width: 768px){.testAnswers{max-width:100%}}.testAnswers__answer{font-weight:300;position:relative;margin:0.3em 0}.testAnswers__answer input{position:absolute;left:-1em;top:0.4em;opacity:0}@media (max-width: 768px){.testAnswers__answer input{top:0.3em;left:-1.3em}}.testAnswers__answer:before{content:"";display:block;position:absolute;left:-1em;top:0.4em;width:0.5em;height:0.5em;border:1px #000000 solid;border-radius:50%}.testAnswers__answer:hover{color:#f86d3c;cursor:pointer;transition:color .3s ease-in}.testAnswers__answer:hover:before{border-color:#f86d3c;transition:border-color .3s ease-in}.testAnswers__answer:active:before,.testAnswers__answer:checked:before{box-shadow:inset 0 0 0 2px #ffffff;background:#f86d3c}@media (max-width: 768px){.testAnswers{margin-top:3em}}@media (max-width: 425px){.testAnswers{margin-top:1.5em}}

.progressBar{display:flex;justify-content:space-between;align-items:center;margin-bottom:5%;margin-left:-12%;width:124%}@media (max-width: 768px){.progressBar{margin-bottom:10%}}.progressBar__item{width:100%;display:flex;align-items:center;justify-content:center;position:relative}.progressBar__item .circle{display:flex;width:.6em;height:.6em;border:1px #000000 solid;border-radius:50%;align-items:center;padding:2px}.progressBar__item .circle:before,.progressBar__item .circle:after{content:"";display:block;background-color:#000000}.progressBar__item .circle:before{height:100%;width:100%;border-radius:50%}.progressBar__item .circle:after{position:absolute;height:2px;width:calc(100% - (.6em + 4px));margin-left:calc(.6em + 2px)}.progressBar__item .active{border-color:#F68657}.progressBar__item .active:before,.progressBar__item .active:after{background-color:#F68657}.progressBar__item .active:after{height:4px}.progressBar__item .animate:after{animation:fill 0.7s ease-in}@keyframes fill{from{background-color:#000000;opacity:0.7}to{background-color:#F68657}}.progressBar__item:last-child .circle:after{width:0;height:0}

.result{background-color:#ffffff;background-image:url("https://www.transparenttextures.com/patterns/blizzard.png");font-size:calc(14px + 11 * ((100vw - 320px) / 1380));min-width:85%;box-sizing:border-box;padding:4%;margin-bottom:5%;border:2px #000000 solid;border-radius:30px;text-align:center;font-family:'Montserrat', sans-serif;font-weight:400}.result__title{color:#1E1407;max-width:70%;margin:2em auto;line-height:1.3em}.result__img{display:block;height:450px;margin:0 auto;object-fit:cover;max-width:70%}@media (max-width: 1024px){.result__img{height:300px}}@media (max-width: 768px){.result__img{height:250px}}@media (max-width: 425px){.result__img{height:200px}}.result__btn{box-sizing:border-box;margin-top:3em;border:2px #000000 solid;border-radius:1em;padding:0.5em 1.7em;background:transparent}.result__btn:hover{background-color:#F68657;border-color:#F68657;color:#F2F2F2;transition:all .3s ease-in}

